.button__container {
    width: 200px;
    height: 200px;
    border-radius: 10px;
    background-size: contain;

    &:hover {
        border: 4px solid #FFFFFF;
        border-radius: 10px;
    }

    .button__img {
        background-size: contain;
        width: 200px;
        height: 200px;
    }
}